@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);
@import url(https://fonts.googleapis.com/css2?family=Jua&display=swap);
.book-card{display:flex;align-items:center;justify-content:center;border-radius:5%;border:1px solid #e4e4e4;text-align:center;width:210px;height:300px;margin:20px 15px 20px 15px;background:#fdfdfd;transition:all .25s linear;box-shadow:0px 1px 2px 0px rgba(134,134,134,0.4);cursor:pointer}.book-card .book-thumbnail{width:150px;height:200px;border:solid #e7e7e7;border-width:0.5px}.book-card .book-title{margin:5px 4px 0px 4px;font-size:16px}.book-card .book-publisher{margin:3px 4px 0px 4px;font-size:15px}.book-card .book-register{font-size:13px}.book-card .borrow{font-size:15px}.book-card .borrow-enable{color:#22CC88}.book-card .borrow-unable{color:#fd2828}.book-card:hover{box-shadow:-1px 3px 20px 0px rgba(161,161,161,0.8)}

.book-search-bar{margin:50px 35px 0px 15px}.book-category{margin-top:60px;margin-left:15px;font-size:28px}.book-list-section{display:flex;flex-wrap:wrap;align-items:center}

.main-nav-list{width:73%}.main-nav-list .main-nav-box{display:flex;float:right}.main-nav-list .main-nav-box .main-nav-item{width:125px;max-width:350px;text-align:center;font-size:20px;float:right;padding:24px 0}.main-nav-list .main-nav-box .main-nav-item :hover{color:#6bd16b}

.main-nav{display:flex;height:65px;border-bottom:solid #e7e7e7;border-bottom-width:0.5px}.main-nav .main-nav-logo{width:21%;text-align:center;padding:5px 0px}.main-nav .main-nav-logo .logo{width:55px}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;font-family:"Spoqa Han Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{margin:0;padding:0;line-height:1.5em}form{margin:0;padding:0}hr{display:none}p,div,th,td,select,input{color:#000000}a:link,a:visited{color:#000000;text-decoration:none}a:active,a:hover{color:#000;text-decoration:none}a img,input.type-image{border:0 none}input.type-text,textarea{border:1px solid #ddd;background:#fff}input.type-text:hover,input.type-text:focus,textarea:hover,textarea:focus,select:hover,select:active{background-color:#ffd}input,select,textarea{vertical-align:middle;font-size:1em;color:#333}select{font-size:11px;font-family:Dotum, "돋움", sans-serif}span.button,img.button,a.button{cursor:pointer;vertical-align:middle}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}

.footer{width:100%;padding-bottom:30px;background-color:#f5f5f5;margin-top:55px}.footer .footer-icons{display:flex;justify-content:center;align-items:center;margin-top:23px}.footer .footer-icons .footer-icon{margin:40px 15px 15px 15px;width:50px;height:50px}.footer .inquire{text-align:center;margin-bottom:9px;font-size:17px}.footer .inquire p{color:#555}.footer .copyright{display:block;text-align:center;font-size:17px}

.main-welcome{text-align:center;margin-top:35px}.main-welcome .ago-img{max-width:330px;max-height:330px}

.rent-button{background-color:#22CC88 !important;color:white !important;height:32px !important}

.book-detail{display:flex;margin-top:30px}.book-detail .book-thumbnail{width:210px;height:280px;border:solid #e7e7e7;border-width:0.5px}.book-detail img{width:500px;margin-right:20px}.book-detail .book-detail-description .title{font-size:x-large;margin:5px 0 10px 0}.book-detail .book-detail-description ul>li{margin-top:4px}

.rental-table{height:400px;margin-top:50px}.rental-table .table-title{font-size:x-large}

.book-search-item>img{width:120px}

.no-result{margin-top:45px;margin-bottom:calc(100vh - 463px)}.less-than-five-item-result{display:flex;flex-wrap:wrap;align-items:center;margin-top:45px;margin-bottom:calc(100vh - 805px)}.more-than-five-item-result{display:flex;flex-wrap:wrap;align-items:center;margin-top:45px}

.book-add-popup-form{display:flex;flex-direction:column;margin:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.book-add-popup-form .form-control{min-width:120px}

*{font-family:'Jua', sans-serif !important}

